.info
{
    position:absolute;
    top:5%;
    left:8%;
    width:80%;
    height:80%;
    background:rgba(54, 58, 65, 0.897);
    text-align:left;
    padding:40px;
    border-radius: 40px;
    box-shadow: 1px 4px 20px -8px black;
    z-index: 10;
    transition : all 1s ease-in;
}
.info >a
{
    color:white;
    text-transform: none;
}
.back-button
{
    padding: 0px;
    border: none;
    background:transparent;
    outline:none;
    border-radius: 13px;
    padding:10px;
    font-size: 18px;
    font-weight:bold;
    color:white;
    margin-left: 90%;
    margin-bottom: 0px;
}
.back-button:hover{
    transform:scale(1.2);
}
.page-btn
{
    border: none;
    background:#393e46;;
    outline:none;
    border-radius: 5px;
    padding:8px;
    font-size: 18px;
    font-weight:bold;
    color:white;
    margin-right: 20px;
}
.question
{
    margin:30px 0px;
    font-size: 30px;
}
.answer
{
    font-size:20px;
}
.author-info
{
    position: absolute;
    bottom:10px;
}